import './style.css';
import { useHistory } from 'react-router';
import db from '../../db/mock';

function Cover() {

    const history = useHistory();
 
    const handleClick = () => {
        history.push('/playlist');
    }

    const importImg = (name: string) => {        
        const ctx = require.context('../../img', false, /\.(gif|jpe?g|tiff?|png|webp|bmp)$/i);
        const img: any = ctx.keys().map(ctx);
        for (let i = 0; i < img.length; i++) {
            if (img[i].default.includes(name.split('.')[0])) {
                return img[i].default;
            }
        }        
    }

    return (
        <div className="cover-container">
            {
                db.playlists.map(element => {
                    return <div onClick={() => handleClick()} className="cover-item">
                        <img src={importImg(element.cover)} alt={element.name}/>
                        <span>{element.name}</span>
                    </div>
                })   
            }
        </div>
    );
}

export default Cover;
